.loading-spinner {
    background-color: rgba(0, 0, 0, 0.1);
    display: none;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    transition: all 1.5s;
    width: 100%;
    z-index: 2;

    &.is-visible {
        display: block;
    }

    &__logo,
    &__indicator {
        border-radius: 60px;
        height: 60px;
        top: 50%;
        left: 50%;
        margin-left: -30px;
        margin-top: -30px;
        position: absolute;
        width: 60px;
    }

    &__logo {
        background-image: url('data:image/svg+xml;base64,');
        background-repeat: no-repeat;
        background-position: 3px center;
        background-size: contain;
        z-index: 2;
    }

    &__indicator {
        animation: pulse 1.5s ease-in-out infinite;
        animation-fill-mode: forwards;
        background-color: rgba(238, 41, 61, 1);
    }
}