.DayPicker {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    position: relative;
    user-select: none;

    &,
    & *:focus {
        box-shadow: none;
        outline: none;
    }
}

.DayPicker-Month {
    display: table;
    border-collapse: collapse;
    border-spacing: 0;
    user-select: none;
    width: 100%;
}

.DayPicker-NavBar {
    position: absolute;
    left: 0;
    right: 0;
    padding: 0 .5rem;
}

.DayPicker-NavButton {
    position: absolute;
    width: 1.2rem;
    height: 1.2rem;
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    cursor: pointer;
    top: 0.8rem;

    &--prev {
        left: 0;
        background-image: url("data:image/svg+xml;base64,");
    }

    &--next {
        right: 0;
        background-image: url("data:image/svg+xml;base64,");
    }
}

.DayPicker-Caption {
    display: table-caption;
    height: 1.5rem;
    text-align: center;
}

.DayPicker-Weekdays {
    display: table-header-group;
}

.DayPicker-WeekdaysRow {
    display: table-row;
}

.DayPicker-Weekday {
    display: table-cell;
    padding: .5rem;
    font-size: .875em;
    text-align: center;
    color: $dark-gray;
}

.DayPicker-Body {
    display: table-row-group;
}

.DayPicker-Week {
    display: table-row;
}

.DayPicker-Day {
    display: table-cell;
    overflow: hidden;
    padding: .2rem;
    position: relative;
    border: 1px solid #eaecec;
    text-align: center;
    cursor: pointer;
    vertical-align: middle;
    @include material-hover(rgba(158, 158, 158, 0.6));

    &:hover {
        background-color: lighten(rgba(158, 158, 158, 0.6), 30%);
    }

    &--today {
        color: $red;
        font-weight: 500;
    }

    &--isDisabled {
        color: #dce0e0;
        cursor: default;
        background-color: $white;
        opacity: 0.9;

        &:hover {
            background-color: $white;
        }
    }

    &--outside {
        cursor: default;
        color: #dce0e0;
    }

    &--sunday {
        color: #dce0e0;
        background-color: #f7f8f8;
    }

    &--selected:not(.DayPicker-Day--isDisabled):not(.DayPicker-Day--outside) {
        color: $white;
        background-color: $blue;
    }

    .DayPicker--interactionDisabled & {
        cursor: default;
    }
}
